.experiencia {
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: var(--blanco);
    font-weight: 400;
    width: 100%;
    margin-bottom: 2rem;
    transition: 1s ease-in-out;
    padding-top: 3rem;
    gap: 2rem;
}

.experiencia-container {
    width: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    gap: 2rem;
}

.experiencia-container:hover> :not(:hover) {
    opacity: 0.8;
    filter: saturate(0%);
}

.experiencia-card-container {
    width: 550px;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: var(--gris);
    padding: 0rem;
    border-radius: 1rem;
    transition: .3s ease-in-out;
}
.experiencia-card-container:hover {
    filter: saturate(130%);
}
.experiencia-card-title {
    margin: 0;
    padding: 1rem;
    font-size: 24px;
    font-weight: 600;
    color: var(--primario);
}

.experiencia-fecha {
    margin: 0;
    padding: 1rem;
    font-size: 18px;
    color: var(--claro);
    font-weight: 100;
}

.experiencia-card-desc {
    margin: 0;
    list-style-type: none;
    background-color: var(-);
    justify-content: start;
}

.experiencia-dato {
    margin: 0;
    padding: .8rem 1rem;
    font-size: 14px;
    color: var(--blanco);
}

.experiencia-promedio {
    margin: 0;
    font-size: 18px;
    color: var(--claro);
}

.experiencia-img {
    width: calc(100% - 1rem);
    height: auto;
    margin: 0;
    padding: 1rem .5rem;
    background-color: var(--blanco);
    border-radius: 0rem 0rem 1rem 1rem;
    transition: 0.3s ease-in-out;
}

.experiencia-card-container:hover .experiencia-img {
    animation: moveUpDown 1s ease-in-out infinite;
}

@keyframes moveUpDown {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-5px);
    }
    100% {
        transform: translateY(0);
    }
}

@media only screen and (max-width: 850px) {
    .experiencia-card-container {
        width: 45%;
    }
}



@media only screen and (max-width: 430px) {
    .experiencia-card-container {
        width: 95%;
    }
    .experiencia-container {
        gap: 4rem;
    }
}